<template>
  <div class="intr">

    <!-- v-model 默认绑定选中标签的索引值，通过修改 v-model 即可切换选中的标签。 -->
    <van-tabbar v-model="active">
  <van-tabbar-item icon="home-o" @click="toIndex">主页</van-tabbar-item>
  <van-tabbar-item icon="orders-o" @click="toWordsIndex">单词</van-tabbar-item>
  <van-tabbar-item icon="bar-chart-o" @click="showChart">数据</van-tabbar-item>
  <van-tabbar-item icon="other-pay" @click="toIntroduction">简介</van-tabbar-item>
</van-tabbar>

  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
    };
  },
  methods:{

    toIndex() {
      this.$router.push('/phone/index')
    },
    toIntroduction() {
      this.$router.push('/phone/introduction')
    },
    showChart() {
      this.$router.push('/phone/chart')
    },
    toWordsIndex(){
       this.$router.push('/phone/WordsIndex')
    }
  }
};
</script>
